<!-- 瀑布图 - 每月收支情况

 -->

<template>
  <div class="demo">
    <div class="box">
      <h3>瀑布图 - 每月收支情况</h3>
      <div id="container"></div>
    </div>
  </div>
</template>

<script>
import { Waterfall } from '@antv/g2plot'

export default {
  components: {},
  data() {
    return {}
  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const data = [
      { type: '日用品', money: 120 },
      { type: '伙食费', money: 900 },
      { type: '交通费', money: 200 },
      { type: '水电费', money: 300 },
      { type: '房租', money: 1200 },
      { type: '商场消费', money: 1000 },
      { type: '红包收入', money: -2000 }
    ]

    const waterfallPlot = new Waterfall('container', {
      data,
      xField: 'type',
      yField: 'money',
      appendPadding: [15, 0, 0, 0],
      meta: {
        type: {
          alias: '类别'
        },
        money: {
          alias: '收支',
          formatter: v => `${v} 元`
        }
      },
      label: {
        style: { fontSize: 10, fill: 'rgba(0,0,0,0.65)' },
        layout: [{ type: 'interval-adjust-position' }]
      },
      total: {
        label: '总支出',
        style: {
          fill: '#96a6a6'
        }
      }
    })

    waterfallPlot.render()
  },
  methods: {},
  created() {
    // var wrapperElement = '<div style=position:relative;></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo {
  display: flex;
  flex-flow: wrap;

  .box {
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display: inline-block;
    padding: 0 20px 20px;
    margin: 20px;

    h3 {
      line-height: 50px;
      height: 50px;
      text-align: center;
    }
  }

  #container {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerG2 {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGz {
    display: block;
    width: 500px;
    height: 300px;
  }
}
</style>



